					temperature = buf.getInt32(1) / (1 << 16);
					const bp = new BridgePacket();
					bp.set({ name: '0', type: 'g', value: RoundDouble(temperature, 2) });
					bp.sendToChannel(ch, BP.TEMPERATURECHANGE);
